<template>
    <div id="account">
      <div v-show ="!isloginin">
      <div class="up">
        <div><span :class="classname" @click="haschange">账号密码登录</span></div>
        <div><span :class="classna"  @click="changehas">手机快捷登录</span></div>
      </div>
      <div class='down' v-if="ischange">
          <div><mt-field label="账号：" placeholder="请输入手机号或用户名" v-model="account"></mt-field></div>
          <div><mt-field label="密码：" placeholder="请输入登录密码" type="password" v-model="password"></mt-field></div>
      </div>
      <div class="down"  v-else>
          <div><mt-field label="邮箱：" placeholder="请输入手机号"></mt-field></div>
          <div><mt-field label="验证码：" placeholder="请输入验证码">
            <img src="#">
          </mt-field></div>
          <div><mt-field label="密码：" placeholder="请输入短信验证码" ></mt-field></div>
      </div>
      <div class="button">
          <mt-button type="primary" size='large' @click="alert">登录</mt-button>
      </div>
      </div>
        <div class="detail" v-if="isloginin">
              <div>登录成功!!!!</div>
              <input type="button" value="退出当前账号" @click="exit">
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui'
import { mapState } from 'vuex'
export default {
  name: 'account',
  data () {
    return {
      ischange: true,
      classname: 'normal use',
      classna: 'normal',
      isloginin: false
    }
  },
  computed: {
    ...mapState(['acc', 'pass']),
    account: {
      get () {
        return this.acc
      },
      set (val) {
        this.$store.state.acc = val
        return this.$store.state.acc
      }
    },
    password: {
      get () {
        return this.pass
      },
      set (val) {
        this.$store.state.pass = val
        return this.$store.state
      }
    }
  },
  methods: {
    haschange () {
      this.classname = 'normal use'
      this.classna = 'normal'
      this.ischange = true
    },
    changehas () {
      this.classname = 'normal'
      this.classna = 'normal use'
      this.ischange = false
    },
    alert () {
      if (this.account === '' || this.password === '') {
        Toast({
          message: '请填写用户名和密码!',
          duration: 1500
        })
      } else {
        window.localStorage.setItem('userinfo', JSON.stringify(this.account))
        Toast({
          message: '登录成功！',
          duration: 1500
        })
        this.isloginin = true
      }
    },
    exit () {
      console.log('nihao')
      window.localStorage.removeItem('userinfo')
      window.localStorage.removeItem('l-cart')
      this.account = ''
      this.password = ''
      this.$router.history.push('/home')
    }
  },
  mounted () {
    if (window.localStorage.getItem('userinfo')) {
      this.isloginin = true
    }
  }
}
</script>

<style lang="scss" scoped>
#account{
  width:100%;
  display: flex;
  background: #eee;
  flex-direction: column;
  .up{
    margin-top:5px;
    width:100%;
    height:40px;
    background:#fff;
    display:flex;
    justify-content:space-around;
    align-items: center;
    font-size:13px;
    .normal{
      height:40px;
      padding:0 30px;
      display:flex;
      justify-content:space-around;
      align-items: center;
      font-size:13px;
    }
    .use{
      border-bottom:2px solid green;
    }
  }
  .down{
    width:100%;
    margin-top: 2px;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    div{
      display: flex;
      justify-content: center;
      width: 100%;
      border-bottom:1px solid #dedede;
    }
  }
  .button{
    margin:20px 15px;
    .mint-button{
      background: rgb(34, 162, 122);
    }
  }
  .detail{
    text-indent: 2em;
    font-size: 15px;
    height: 50px ;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}
</style>



